.bottom{
  width:100%;
  height:50%;
  background-color:#282828;
  color:#fff;

}
.bottom1{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;

}
.bottommain{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}
.h4{
  text-align: center;
  font-size: 120%;
}
.h5{
  font-size: 100%;
 text-align: center;
}
.p1{
  font-size: 90%;
  text-align: left;
}


/* Desktops and laptops ----------- */
@media (min-width:1224px){
  .h5{
    font-size: 120%;
   text-align: center;
  }
  .p1{
    font-size: 90%;
    text-align: left;
  }

}



