.Appbar{
height:10%;
width:100%;
background-color: #000;
opacity: 0.7;
display:-ms-flexbox;
display:flex;
-ms-flex-pack: distribute;
    justify-content: space-around;
color: #fff;
font-size: 12px;
-ms-flex-align: center;
    align-items: center;
position: fixed;
z-index: 3;
visibility: hidden;
}
.Appbar2{
  display: -ms-flexbox;
  display: flex;
}
.Appbar1{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  opacity: 0.5;
}
.Appbar1:hover{
  opacity:1;
}
.Appbarmob{
  height:30px;
  width:100%;
  background-color: #000;
  opacity: 0.7;
  z-index: 1;

}
/* Desktops and laptops ----------- */
@media (min-width:1224px){
.Appbar{
  visibility: visible;
}

}



