.About{
  background-color: #fff;
  width:80%;
  height:auto;
  margin-left: 10%;
  border-color: #000;

}
.head{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;

}
.content1{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

}
.Content{
  font-size: 120%;
  font-style: italic;
  color: #575757;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-item-align: center;
      align-self: center;
}
.image1{
  width: 60%;
  height:60%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-item-align: center;
      align-self: center;
}
/* Desktops and laptops ----------- */
@media (min-width:1224px){
  .content1{
    display: -ms-flexbox;
    display: flex;
      -ms-flex-direction: row;
          flex-direction: row;
  }
  .image1{
    width:75%;
    height:75%;
  }

}
@media (max-width:600px){
  .content1{
    display: -ms-flexbox;
    display: flex;
      -ms-flex-direction: row;
          flex-direction: row;
  }
  .image1{
    width:75%;
    height:75%
  }

}



